code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
		monospace;
}

a,
a:visited {
	color: inherit;
	text-decoration: none;
}

audio,
canvas,
progress,
video {
	display: inline-block;
}

img {
	max-width: 100%;
}

button {
	border-style: none;
	cursor: pointer;
	box-shadow: 0 2px 6px rgba(20, 20, 20, 0.16);
}

button:hover {
	box-shadow: 4px 4px 8px rgba(20, 20, 20, 0.16);
}

audio:not([controls]) {
	display: none;
	height: 0;
}

ul,
ol,
li {
	list-style: none;
	padding: none;
}

legend {
	background-color: #000;
	color: #fff;
	padding: 3px 6px;
}

::-webkit-scrollbar {
	width: 8px;
	height: 6px;
}

::-webkit-scrollbar-track {
	background-color: #f5f5f5;
}

::-webkit-scrollbar-thumb {
	background: royalblue; /* linear-gradient(#16df84, #162432); */
	border-radius: 4px;
}

.bg_WhiteSmoke {
	background-color: #f5f5f5;
}

.debugger {
	background-color: antiquewhite;
	width: 100%;
	margin: 4vh auto;
	padding: 1rem;
	text-align: start;
}

.external_Link {
	color: salmon;
}

.material_icons {
	font-size: inherit;
	color: rebeccapurple;
}

.post:hover {
	background: #16df8480;
}

.highlight {
	padding: 0 2px 0 2px;
	border-bottom: 2px solid #16df84;
	box-shadow: inset 0 -3px 0 #16df84;
}

section {
	scroll-behavior: smooth;
	scroll-snap-type: y proximity;
}

@media only screen and (min-width: 481px) {
	.debugger {
		width: 80%;
		padding: 2rem;
		text-align: start;
	}
}
